

.footer{
    width: 100%;
    padding: 6rem 0;
    background-color:rgba(19, 19, 19, 0.8);
    /* display: flex;
    justify-content: space-between;
    align-items: center; */
}
.footer-container{
    max-width: 1140px;
    margin:auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:40px;

}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location ,.phone ,.email{
    display: flex;
    margin-bottom: 0.8rem;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;

}

.social{
    height: 100%;
    max-width: 100%;
    margin-top: 0.8rem;
}

h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

p{
    font-size:1.2rem;
}


/* making the footer responsive for the 
use of small screen device */

@media screen and (max-width:640px) {
    .footer-container{
        /* max-width: 1140px;
        margin:auto;
        display: grid; */
        grid-template-columns: 1fr;
        /* grid-gap:40px; */
    
    }    
}
